import React from "react";
import {Link} from "react-router-dom";

const CommentsList = ({articleInfo}) => (
    <>
        {articleInfo.comments.map((comment, key) => (
            <p>
                <h4>{comment.username}</h4>
                <p>{comment.text}</p>
            </p>
        ))}
    </>
);

export default CommentsList;